.side-bar{
    position: fixed;
    top: 0px;
    right: 0px;
    height: 100vh;
    width: 500px;
    background-color: #0e0c16;
    overflow-y: scroll;
}

.arrow-btn{
    height: 45px;
    width: 45px;
    border-radius: 100px;
    background-color: #5918df;
    margin: 40px 0px 0px 40px;
}

.back-arrow{
    position: relative;
    top: -1px;
    left: 7px;
    font-size: 30px;
    font-weight: 100;
    color: #f9f9f9;
}

.btnlink{
    position: relative;
    top: -3px;
    left: 4.5px;
    font-size: 30px;
    font-weight: 100;
    color: #f9f9f9;
}

.skillset{
    padding: 10px 50px;
}

.skills{
    color: #988e9f;
    font-weight: 400;
    font-family: Roboto Condensed, Lato, Arial, sans-serif !important;
    font-size: 24px;
    line-height: 1.3;
    word-spacing: 5px;
    letter-spacing: 0.5px;
}

.title{
    padding-left: 50px;
}

.project-title{
    color: #f9f9f9;
    font-weight: 700;
    margin: 0px 0px 0px 0px;
    font-size: 38px;
    font-family: Roboto Condensed, Lato, Arial, sans-serif !important;
}

.content{
    padding-left: 50px;
    padding-right: 30px;
}

.description{
    font-size: 25px;
    color: #f9f9f9;
    font-weight: 300;
}

.btn{
    padding-left: 50px;
    padding-top: 30px;
    padding-bottom: 50px;
}

a{
    text-decoration: none;
    font-size: 25px;
    font-weight: 400;
    font-family: Roboto Mono, Courier New, Courier, monospace !important;
    color: #f9f9f9;
}

.visit-btn{
    height: 60px;
    width: 150px;
    border: 0px;
    text-decoration: none;
    background-color: #5918df;
    border-radius: 50px;
}

.visit-btn:hover{
    transition: transform .3s cubic-bezier(.175,.885,.32,1.275), bottom .3s cubic-bezier(.175,.885,.32,1.275), background-position 3s cubic-bezier(.455,.03,.515,.955);
}

video{
    width: 1040px;
    height: 600px;
}


.image{
    background-image: url("C:\Users\SATWIK\OneDrive\Desktop\portfolio-pages\src\netfliximage.png");
    width: 1040px;
    height: 3000px;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
}

/*Samsung S8+*/
@media (min-width: 360px) {
    /**/
    .side-bar{
        position: relative;
        top: 0px;
        left: 0px;
        height: max-content;
        width: 100%;
        overflow-y: visible;
        background-color: #0e0c16;
        padding-top: 10px;
    }
    
    .description{
        font-size: 20px;
    }
    
    video{
        width: 100%;
        height: 250px;
    }
    
    .image{
        width: 100%;
        height: 1200px;
    }
}

